import React from 'react'
import Proptypes from 'prop-types'
import { BdsIcon } from 'blip-ds/dist/blip-ds-react';

export const PageHeaderComponent = ({ title, logo }) => (
    <>
        <div className="header">
            <div className="dt dt--fixed">
                <div className="dtc tl v-mid w-90">

                    <h1 className="bp-fs-3 bp-c-city" ><BdsIcon theme="outline" type="icon" name="plugin" size='large' />{title}</h1>
                </div>
                <div className="dtc w-08">
                    <div className="dt dt--fixed">
                        <div id="logo-area" className="dt-row tc">
                            {logo && (
                                <img
                                    id="logo-img"
                                    className="logo-img"
                                    src={logo}
                                    alt="Uma empresa parceira takeBlip"
                                    class="w-60"
                                />
                            )}<br></br>
                            <span className="bp-c-city" style={{ fontSize: '10px' }}>Version: 1.0</span>
                        </div>
                    </div>
                </div>
            </div>
            <div className="dt dt--fixed">
                <div className="bp-divider-h"></div>
            </div>
        </div>


    </>
)

PageHeaderComponent.propTypes = {
    title: Proptypes.string,
}
